body {
  background-color: #31312f;
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  font-size: 16px;
  line-height: 1.6;
}

.header__logo {
  cursor: pointer;
  background-color: #ba9f9f;
  border: .3px solid #e3dede;
  border-radius: 3%;
  align-items: center;
  max-width: 100px;
  padding: 8px 12px;
