}

.header__content:hover {
  opacity: 1;
  box-shadow: 0 0 20px #ffffff80, 0 0 40px #fff6;
}

.header__burger {
  box-sizing: border-box;
  cursor: pointer;
  z-index: 10;
  background-color: #635a5103;
  border: 1.6px solid #5c5050;
  border-radius: 8px;
  width: 50px;
  height: auto;
  padding: 15px;
  transition: transform .6s ease-in-out;
  position: absolute;
  top: 15px;
  right: 30px;
  box-shadow: 0 4px 10px #f0eae980;
}

.header__burger:hover {
  transform: scale(1.2);
}

.header__burger:active {
  transform: scale(.95);
}

.header__chili-pizza {
  object-fit: cover;
  border: 3px solid #c5bbbb;
  border-radius: 10%;
